
import React, { Component } from 'react'
import OneSong from './OneSong';
export class ListAlbum extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tracks: [],
      playing: 0,
      albumID: this.props.id,
      album: '',
      listTracks: '',
      isLoad: false
    };
  }
  componentWillReceiveProps(nextProps) {
    console.log(nextProps);
    if (nextProps.chooseTrack !== '') {
      this.setState({
        playing: nextProps.chooseTrack.album.playing
      });
    }
    if (nextProps.inputVal === true) {
      this.setState({
        playing: 0,
      });
    } else if (nextProps.inputVal === true) {
      this.setState({
        playing: '',
      });
    }

  }
  componentDidMount = () => {
    if (this.props.chooseTrack === "") {
      const url = `https://raw.githubusercontent.com/hailduong/lyduong/master/react-assignment/spotify-2/apis/yourLibrary.json`;
      fetch(url)
        .then(respone => respone.json())
        .then(data => {
        
          this.setState({
            album: data[this.props.id - 1]
          });
        })
        .catch(e => console.log('error', e));
    }
    const url = `https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/album/${this.props.id}.json`;
    fetch(url)
      .then(respone => respone.json())
      .then(data => {
        console.log(data);
        var dataChangeType = data.tracks;
        dataChangeType.map((item) => {
          item.length = this.changeTime(item.length);
        })
        this.setState({
          tracks: data.tracks,
          listrack: data,
          isLoad: true
        })
      })
      .catch(e => console.log('error', e));
  }

  checkSong(index) {
    this.setState({
      playing: index
    }, () => {
      const stateSend = {
        album: this.props.chooseTrack.album.album,
        playing: this.state.playing,
        tracks: this.props.chooseTrack.album.tracks
      }
      console.log(stateSend);
      this.props.update(stateSend);
    });
  };
  changeTime(miliseconds) {
    var minutes = Math.floor(miliseconds / 60000);
    var seconds = ((miliseconds % 60000) / 1000).toFixed(0);
    return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
  }

  render() {
    return (
      <div className="list-content">
      
      {
          this.state.tracks.map((item , index) => {
            const active =this.state.playing === index ? 'active' : '';
            return (
              <div className="list-music" 
            onClick={() => this.checkSong(item,index)}
            >
            <OneSong
              newTrack = {item}
              className ={active}
            />
           </div>
            )
            
          })
        }
     
        
      </div>
        
          
    )
  }
}

export default ListAlbum
