import React from 'react';
const ListRecentPlayed = props => (
    <div className="leftmenu-content2">
        {props.recentPlay.recentlyPlayed.map((item, index) => 
            <div className="recent-list" key={index}>

                <div className="list-name">{item.name}</div>
                <div>{item.type}</div>

            </div>


    )}
    </div>
)
export default ListRecentPlayed;