import React, { Component } from 'react'
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import './Leftmenu.css';

import ListRecentPlayed from './ListRecentPlayed';

import App from './../App';
import Library from './Catagory/Library';
import SpotifyHome from './SpotifyHome';
import Search from './Search';
import RightContent from './RightContent';


export class Leftmenu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            green:true,
            user: {
                playing :[],
                recentlyPlayed: [],
                recentlyPlayedType : []
            }
        };
    }
  
    getUserData(){
        fetch('https://hailduong.github.io/lyduong/react-assignment/spotify/apis/userInfo.json')
        .then(response => response.json())
        .then(data => {
           var typeP = data.recentlyPlayed
           typeP.map((item, index) => {
                if(item.type === "1"){
                    item.type = "Album";
                } else if( item.type === "2"){
                    item.type ="PlayList"
                } 
           })
            this.setState({
                user: data
               
            })
        }).catch(error => console.log(error));
    }
    componentWillMount() {
        this.getUserData();
    }
   
    render() {
       
        return (
            <div className="left-content">
              

                
                    <div>
                        <div className="left-title"><span><i className="fab fa-spotify">Spotify</i></span></div>
                        <div className="col-lg-12 col-md-12">
                          
                          
                            <ul className="leftmenu">
                                <li>

                                    <Link to="/"> <i className="fas fa-home">Home</i></Link>
                                </li>
                                <li>
                                    <Link to="/search"> <i className="fa fa-search" aria-hidden="true">Search</i></Link>
                                </li>
                                <li>
                                    <Link to="/library"><i className="fa fa-book" aria-hidden="true">Your Library</i></Link>
                                </li>
                            </ul>
                            <span>Recent Played</span>
                           
                            <ListRecentPlayed recentPlay = {this.state.user}/>
                            
                            <footer>
                                <div className="left-content-footer">
                                    <i className="far fa-arrow-alt-circle-down"> Install App</i>
                                    <hr />
                                    <img src={this.state.user.avatar} className="avatar" /><span>{this.state.user.name}</span>
                                </div>

                            </footer>
                        </div>

                        
                        
                        

                    </div>
                   
            </div>



        )
    }
}

export default Leftmenu
