import React, { Component } from 'react'
import './RightContent.css';
export class middle extends Component {
    constructor(props) {
        super();
        this.state = {
            name: '',
            artis: '',
            cover: '',
            year: '',
            track: [],
            show: true,
        }
    }
    componentWillMount = () => {

        const url = `https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/album/${this.props.id}.json`;
        fetch(url)
            .then(response => response.json())
            .then(data => {
                this.setState({
                    name: data.name,
                    artis: data.artis,
                    cover: data.covers.large,
                    year: data.year,
                    track: data.tracks.length,
                });
            }, () => console.log(this.state))
            .catch(e => console.log('error', e));
    }
    toogleShow = () => {
        if(this.state.show === true) {
            this.setState((prevState) => ({
                    show : false
            }));
            this.props.action(true);
        }else if( this.state.show === false){
            this.setState((prevState) => ({
                show : true
            }));
            this.props.action(false);
        }
    }
    render() {
        return (
            <div>
                <div className="img-header">
                    <img src={this.state.cover} className="img-header" />
                </div>

                <div className="song-content">
                    <div className="song-content-header">
                        <div className="title">
                            <span>{this.state.name}</span>
                        </div>
                        <div className="singer">
                            {this.state.artist}
                        </div>
                    </div>
                    <div className="song-content-body">
                    {this.state.show ? 
                        <button type="button" className="playBtn" onClick={this.toogleShow}>Play</button>
                        : ''
                    }
                    {!this.state.show ? 
                        <button type="button" className="playBtn" onClick={this.toogleShow}>Pause</button>
                        : ''
                    }
                        <br />
                        <span>{this.state.year} . {this.state.track}</span>
                    </div>
                    <br />

                    <div className="more">
                        <button type="button" className="btn btn-link">
                            Remove from library
                            </button>
                        <i className="fas fa-ellipsis-h"></i>
                    </div>

                </div>
            </div>
        )
    }
}

export default middle
