import React, { Component } from 'react'
import "./search.css";

import SearchResult from './SearchResult';
export class Search extends Component {
    constructor(props){
        super(props);
        this.state = {
            value: '',
            tracks: '',
            albums: '',
            artists: '',
            
            activeTrack: '',
            token: '',
            isloaded : false
            
            
            
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleClick = this.handleClick.bind(this);
    }
    handleClick(track){
        this.props.action(track);
    }
    handleSubmit(event) {
        const token = this.props.access_token;
      
        const url = `https://api.spotify.com/v1/search?q=${this.state.value}&type=track,artist&limit=10`;
       
        fetch(url,{
            
            headers : {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`,
            }
        })    
            .then(response => response.json())
            .then(data => {
                var dataChangeType = data.tracks.items;
                dataChangeType.map((item) => {
                item.duration_ms = this.changeTime(item.duration_ms);
        })
                console.log(data);
                this.setState({
                    artists : data.artists.items,
                    isloaded : true,
                    tracks : data.tracks.items,
                    playlists: data.playlists.items
                    //item : data.artist.item
                });
            
                
            })
            .catch(e => {
                window.location.replace("https://accounts.spotify.com/authorize?client_id=e2a42674970c41b6b7b24c6144da9d1d&response_type=token&Foauth_callback%2F&scope=streaming user-read-playback-state user-read-currently-playing user-modify-playback-state&redirect_uri=https:%2F%2Fdastankin.github.io%2FMySpotify");
                console.log('error',e);

            });
            event.preventDefault();
           
        
    }
    handleChange(event){
        this.setState({
            value : event.target.value
        })
    }
    changeTime(miliseconds) {
        var minutes = Math.floor(miliseconds / 60000);
        var seconds = ((miliseconds % 60000) / 1000).toFixed(0);
        return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
      }
    render() {
    
        console.log(this.state.artists);
        console.log(this.state.album);
        console.log(this.state.tracks);
        console.log(this.state.playlists);
        return (   
            <div className="page-content">
                <form className="search" onSubmit={this.handleSubmit} >
                    <input className="form-control search-content" type="text" placeholder="Start Typing....." aria-label="Search" value={this.state.value} onChange={this.handleChange} autoFocus />
                    
                </form>
            
                {/* <SearchResult
                      tracks={this.state.tracks}
                      albums={this.state.albums}
                      artists={this.state.artists}
                     
                      action={this.handleClick}
                /> */}
                
            </div>
        )
   
        //  return (
        //     <div>
        //             <div className="search-title">
        //                 <span>Search Spotify</span><br />
        //                 <span>Find your favourite Song , Artirst, Album ......</span>
        //             </div>
        //         </div>

        //  )
      
     
        
    }
}

export default Search
