import React, { Component } from 'react'
import './footer.css'
import smalllogo from './image/cover-small.jpeg';
export class footer extends Component {
    render() {
        return (
            
                <div className="footer-content">
                    <div className="left-footer col-lg-4 col-md-4">
                        <div className="logo-img" >
                        <img src={smalllogo} className="small-logo"  />
                        </div>
                        
                        <div className="title-music">
                            <div>Moon And Wind</div>
                            <span>Huong Thanh</span>
                        </div>
                        <div className="status">
                            <i className="fas fa-check"></i>
                        </div>

                    </div>
                    <div className="middle-content col-lg-4 col-md-4">
                        <div className="button-play">
                            <div className="button-random">
                                <i className="fas fa-random"></i>
                            </div>
                            <div className="button-step-backward">
                                <i className="fas fa-step-backward"></i>
                            </div>

                            <div className="button-play-circle">
                                <i className="far fa-play-circle"></i>
                            </div>
                            <div className="button-forward">
                                <i className="fas fa-step-forward"></i>
                            </div>
                            <div className="button-redo">
                                <i className="fas fa-redo"></i>
                            </div>
                        </div>
                        <div className="progress">
                                <div className="progress-bar w-75" role="progressbar" aria-valuenow="75" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>
                        
                    </div>
                    <div className="right-footer col-lg-4 col-md-4">
                        <div className="button-play">
                            <div className="button-layer">
                                <i className="fas fa-layer-group"></i>
                            </div>
                            <div className="button-server">
                                <i className="fas fa-server"></i>
                            </div>

                            <div className="button-volume">
                                <i className="fas fa-volume-up">

                                </i>


                            </div>
                           

                        </div>
                    </div>
                </div>



           

        )
    }
}

export default footer
