import React, { Component } from 'react'

import "./Library.css";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
export class Library extends Component {
  constructor(props) {
    super(props);
    this.state = {
      albums: []

    }

  }
  getLibraryData() {
    fetch('https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/yourLibrary.json')
      .then(response => response.json())
      .then(data => {
        
        this.setState({
          albums: data
        })
      }).catch(error => console.log(error));
  }

  componentDidMount() {
    this.getLibraryData();
  }

  render() {

    return (
      <div className="category-content">
        <div className="title">Album</div>
        <div className="row">
          {this.state.albums.map((item, index) =>{
            return (
              <div className="col-xl-2 col-md-2 col-lg-2" key={index}>
                <Link to={"/album/" + (index + 1)}>
                <div  onClick={() => this.props.action(item)} className="list-album " >
                  <div className="img-hover-container" >
                    <img src={item.cover} alt="" className="image" />
                    <div className="middle">
                      <div className="text"><i className="far fa-play-circle"></i></div>
                    </div>
                  </div>
                  <div className="album-name">
                    {item.album}
                  </div>
                  <div className="album-artist">
                    <span>Huong Thanh</span>
                  </div>

                </div>
              </Link>
              </div>
             
            )
          }


           
          )}
        </div>
      </div>
    )
  }
}

export default Library
