import React, { Component } from 'react'



const OneSong = props => (
  
    <div className={props.className}>
         <div className="icon ">
          <i className="fas fa-music"></i>
        </div>
        <div className="song-name ">
          {props.newTrack.title}
        </div>
        <div className="song-time col-xl-2 col-lg-2 col-md-2">
          {props.newTrack.legnth}
        </div>
      </div>

    
  );

