import React, { Component } from 'react'
import music from './image/tumblr.gif';
import './SpotifyHome.css';
export class SpotifyHome extends Component {
  render() {
    return (
      <div className = "home-content">
        <div className="home-title"> Welcome to Spotify</div>
        <div className="gif">
           <img src={music} /> 
        </div>
        
      </div>
    )
  }
}

export default SpotifyHome
