import React, { Component } from 'react'
import './RightContent.css';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import logo from './image/test.jpg';
import ListAlbum from './ListAlbum';
import Middle from './Middle';
export class RightContent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isPlay: '',
            choosenTrack: ''
           
        };
        this.playStop = this.playStop.bind(this);
       
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            choosenTrack: nextProps.inputVal,
            isPlay: ''
        })        
    }
    playStop(dataFromChild) {
        if(dataFromChild === true){
            this.setState({
                isPlay : dataFromChild,
            }, () => console.log(this.state));
        } else if(dataFromChild === false){
            this.setState({
                isPlay : dataFromChild,
            }, () => console.log(this.state));
        }
    }
    

    
  
   
    render() {      
        return (
           
            <div className="content">
                <div className="right-side">
                    <div className=" left-list col-md-4 col-lg-4 col-xl-4">
                    <Middle id = {this.props.match.params.id} action={this.playStop}/>
                    </div>
                     
                    
                    <div className="right-content col-md-7 col-lg-7 col-xl-7">
                        <ListAlbum id={this.props.match.params.id} 
                      
                            chooseTrack = {this.state.choosenTrack}
                            inputVal ={ this.state.isPlay}
                            update = {this.props.update}
                        />
                       
                      
                    </div>
                </div>
            </div>



        )
    }
}

export default RightContent;
