import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.css';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Library from './spotify/Catagory/Library';
import SpotifyHome from './spotify/SpotifyHome';
import Search from './spotify/Search';

import { Leftmenu } from './spotify/Leftmenu';
import { RightContent } from './spotify/RightContent';
import Footer from './spotify/footer';
import SpotifyWebApi from 'spotify-web-api-js';
const spotifyApi = new SpotifyWebApi();
class App extends Component {
  constructor(props) {
    super(props)
    const params = this.getHashParams();
    //get access token
    const token = params.access_token;
    if (token) {
      spotifyApi.setAccessToken(token);
      this.state = {
        album: '',
        token: token,
        isPlay: '',
      };
    }
    else {
      this.state = {
        album: '',
        token: '',
        isPlay: '',
      };
    }
   
    this.getAlbum = this.getAlbum.bind(this);
  };
  getHashParams() {
    var hashParams = {};
    var e, r = /([^&;=]+)=?([^&;]*)/g,
      q = window.location.hash.substring(1);
    e = r.exec(q)
    while (e) {
      hashParams[e[1]] = decodeURIComponent(e[2]);
      e = r.exec(q);
    }
    return hashParams;
  }

  getAlbum(dataFromChild) {
    this.setState({
      album: dataFromChild,
    });
  }
  render() {
    
    return (

      <Router>
      <div className="row"  >
        
      
        <Leftmenu />
        <div className="right-content">
        <Route exact path="/" component={SpotifyHome} />
        <Route path="/search" render ={() => <Search access_token={this.state.token}/>}/>
        <Route path="/library" component={Library} access_token={this.state.token} action={this.getAlbum}/>
        <Route path="/album/:id" render = {(props) => <RightContent {...props}   update={this.getAlbum} access_token={this.state.token}/> }/>
        </div>
        
        <Footer inputVal={this.state.isPlay} />
        
       
      </div>
      </Router>
    );
  }
}

export default App;
